.award_container {
    height: 100vh;
    margin: 0;
    background: linear-gradient(90deg,  #3498db 50%, #ffffff 50%);
}

.award_left_content {
    vertical-align: top;
    left: 0;
    top: 0;
    width: 50%;
    height: 100vh;
    overflow-y: scroll;
    margin: 0;
    display: inline-block;
}

.award_right_content {
    vertical-align: top;
    right: 0;
    top: 0;
    width: 50%;
    height: 100vh;
    margin: 0;
    display: inline-block;
}

.award_content_container{
  width: 80%;
  margin: auto;
}

.award_title {
  text-align: center;
  vertical-align: middle;
  padding-top: 40%;
  margin: auto;
  color: #3498db;
  font-size: 7vw;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.award_content{
  text-align: center;
  vertical-align: middle;
  margin: auto;
  color: #ffffff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.award_content .award_content_title{
  font-size: 2vw;
}

.award_content .award_content_company{
  font-size: 1.5vw;
}

.award_content .award_content_desc{
  font-size: 1vw;
}

hr{
  width: 80%;
}